import s from './settings'
import './utils'
import Grid from './grid'
import Cursor from './cursor'

s.install();

const grid_canvas = document.getElementById("grid-canvas") as HTMLCanvasElement;
const grid = new Grid(grid_canvas);
grid.install();

const cursor = new Cursor();

function clear_canvas(canvas_context: CanvasRenderingContext2D): void
{
	canvas_context.clearRect(0, 0, s.canvas_width, s.canvas_height);
}

grid.draw(); // Draw grid once on background canvas
function draw(): void
{
	clear_canvas(s.canvas_context);
	cursor.draw();
	window.requestAnimationFrame(draw);
}

draw();
