import s from './settings'
import { unsafe, grid_x_to_x, grid_y_to_y } from './utils'

export default class Cursor
{
	public glyph: string;
	public x: number;
	public y: number;

	constructor(x: number = 0, y: number = 0)
	{
		this.glyph = "@";
		this.x = x;
		this.y = y;

		window.addEventListener("keydown", this.event_handler(this));
	}

	private event_handler(binding: Cursor) 
	{
		return function (e: KeyboardEvent) 
		{
			const key = e.key;

			switch (key) {
				case 'h': 
					binding.move_left();
					break;
				case 'l':
					binding.move_right();
					break;
				case 'j':
					binding.move_down();
					break;
				case 'k':
					binding.move_up();
					break;
			}
		}
	}
	
	move_up()
	{
		this.x = this.x;
		this.y = unsafe(this.x, this.y - 1) ? this.y : this.y - 1;
	}

	move_down()
	{
		this.x = this.x;
		this.y = unsafe(this.x, this.y + 1) ? this.y : this.y + 1;
	}

	move_left()
	{
		this.x = unsafe(this.x - 1, this.y) ? this.x : this.x - 1;
		this.y = this.y;
	}

	move_right()
	{
		this.x = unsafe(this.x + 1, this.y) ? this.x : this.x + 1;
		this.y = this.y;
	}

	public draw()
	{
		s.canvas_context.fillStyle = s.highlight_bg_color;
		s.canvas_context.fillRect(grid_x_to_x(this.x),
															grid_y_to_y(this.y),
															s.cell_size,
															s.cell_size)
		s.canvas_context.fillStyle = s.highlight_fg_color;
		s.canvas_context.fillText(this.glyph,
															grid_x_to_x(this.x, true),
															grid_y_to_y(this.y, true));
	}
}
